import React from "react";
import { Switch, Route } from "react-router-dom";
import ProductAdd from "./productAdd";
import Dashboard from "./dashboard";
import Registration from "./registration";
import ProductAddRegistration from "./productAddRegistration";
import ProductRegistrationClub from "./productRegistrationClub";
import RegistrationCompetitionForm from "./registrationCompetitionForm";
import CompetitionOpenRegForm from "./competitionOpenRegForm";
import CompetitionFormat from "./competitionFormat";
import CompetitionFinals from "./competitionFinals";
import CompetitionLadder from "./competitionLadder";
import CompetitionCourtAndTimes from "./competitionCourtAndTimes";

const Routes = () => (
  <Switch>
    <Route path="/productAdd" component={ProductAdd} />   /*task demo/*/
    {/* <Route exact path="/" component={Dashboard} /> */}
    <Route exact path="/registration" component={Registration} />
    <Route path="/productAddRegistration" component={ProductAddRegistration} />
    <Route path="/productRegistrationClub" component={ProductRegistrationClub} />
    <Route path="/registrationCompetitionForm" component={RegistrationCompetitionForm} />
    <Route path="/competitionOpenRegForm" component={CompetitionOpenRegForm} />
    <Route path="/competitionFormat" component={CompetitionFormat} />
    <Route path="/competitionFinals" component={CompetitionFinals} />
    <Route path="/competitionLadder" component={CompetitionLadder} />
    <Route path="/competitionCourtAndTimes" component={CompetitionCourtAndTimes} />

  </Switch>
);
export default Routes;
