import React, { Component } from "react";
import { Layout, Breadcrumb, Input, Select, Checkbox, Tree, Button, Icon } from 'antd';
import './competition.css';
import { NavLink } from 'react-router-dom';
import InputWithHead from "../customComponents/InputWithHead";
import InnerHorizontalMenu from "../pages/innerHorizontalMenu";


const { Header, Footer, Content } = Layout;
const { Option } = Select;
const { TextArea } = Input;

class CompetitionOpenRegForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nameChecked: "true",
            roleChecked: "true",
            emailChecked: "true",
            phoneChecked: "true",
            registrationUpdates: "weekly",

        }
    }


    onChange(checkedValues) {
        console.log('checked = ', checkedValues);
    }

    onSelect = (selectedKeys, info) => {
        console.log('selected', selectedKeys, info);
    };

    onCheck = (checkedKeys, info) => {
        console.log('onCheck', checkedKeys, info);
    };


    ///////view for breadcrumb
    headerView = () => {
        return (
            <div className="header-view">
                <div style={{ flexDirection: "row", display: "flex", justifyContent: "center", width: "100%", marginBottom: 30 }}>
                    <div className="reg-competition-search-view" style={{ flexDirection: "column", display: "flex", justifyContent: "center", width: "100%", }}>
                        <div className="product-reg-input-view" style={{ flexDirection: "row", display: "flex", justifyContent: "center", width: "100%", justifyContent: "flex-end" }}>

                            <div style={{ width: 352 }}>
                                <Input
                                    className="product-reg-search-input"
                                    placeholder="Search..."
                                    prefix={<Icon type="search" style={{ color: 'rgba(0,0,0,.25)', height: 16, width: 16 }} />}

                                />
                            </div>
                        </div>

                    </div>
                </div>
                {/* <Header style={{
                    backgroundColor: 'transparent', display: 'flex',
                    alignItems: 'flex-start'
                }}>

                    <Breadcrumb style={{ alignItems: 'center', alignSelf: 'center' }} separator=">">
                        <NavLink to="/registration">
                            <Breadcrumb.Item className="breadcrumb-product">Registration</Breadcrumb.Item>
                        </NavLink>
                        <Breadcrumb.Item className="breadcrumb-add">Create Competitions Stage 1</Breadcrumb.Item>
                    </Breadcrumb>

                </Header> */}
            </div>
        )
    }


    ////////form content view
    contentView = () => {

        return (
            <div className="content-view">
                <span className='form-heading'>Details</span>

                <InputWithHead heading={"Club Name"} placeholder={"Club Name"} />
                <InputWithHead heading={"Club Logo"} />

                <div className="fluid-width" >
                    <div className="row" >
                        <div className="col-sm" >
                            <div className="reg-competition-logo-view">
                                <img src={require('../images/add.png')} height="18" width="18" />
                            </div>
                        </div>
                        <div className="col-sm" style={{ display: "flex", alignItems: "center" }}>
                            <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Use Default</Checkbox>
                        </div>
                    </div>
                </div>

                <InputWithHead heading={"Special Note"} />
                <TextArea placeholder="Enter a short note of the competition..." allowClear />

                <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Reply to contact details</Checkbox>

                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.setState({ nameChecked: e.target.checked })}>Name</Checkbox>
                    {this.state.nameChecked && <div className="reg-competition-repeat-for-view">
                        <InputWithHead placeholder={"Name"} />
                    </div>}
                </div>
                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.setState({ roleChecked: e.target.checked })}>Role</Checkbox>
                    {this.state.roleChecked && <div className="reg-competition-repeat-for-view">
                        <InputWithHead placeholder={"Role"} />
                    </div>}
                </div>
                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.setState({ emailChecked: e.target.checked })}>Email</Checkbox>
                    {this.state.emailChecked && <div className="reg-competition-repeat-for-view">
                        <InputWithHead placeholder={"Email"} />
                    </div>}
                </div>
                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.setState({ phoneChecked: e.target.checked })}>Phone</Checkbox>
                    {this.state.phoneChecked && <div className="reg-competition-repeat-for-view">
                        <InputWithHead placeholder={"Phone"} />
                    </div>}
                </div>
            </div>


        )
    }


    ///how user register view
    UserRegisterView = () => {
        return (
            <div className="inside-form-view">
                <span className='form-heading'>How Users Register</span>
                <div className="checkbox-row-wise">
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>App</Checkbox>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Website</Checkbox>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>At event</Checkbox>
                </div>

            </div>
        )
    }

    ///payment setting view
    PaymentSettingView = () => {
        const paymentGatewaySetting = [
            {
                title: 'Payment Gateway',
                children: [
                    { title: 'Credit Card', children: [] },
                    { title: 'Bank Transfer', children: [] },
                    { title: 'Cash', children: [] },
                    { title: 'Zip Pay', children: [] }
                ]
            }
        ];
        return (
            <div className="inside-form-view">
                <span className='form-heading'>Payment Settings</span>

                <div className="inside-container-view">
                    <Tree className="tree-government-rebate" checkable
                        defaultExpandedKeys={['0',]}
                    // defaultCheckedKeys={['0-0', '0-1']}
                    >
                        {this.ShowVoucherSettingSettingTree(paymentGatewaySetting)}
                    </Tree>
                    <span className='input-heading-add-another'>+ Add Another payment Gateway</span>
                </div>

                <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Allow Offline Payment</Checkbox>
                <div className="fluid-width" >
                    <div className="row" >
                        <div className="col-sm" >
                            <InputWithHead heading={"Tax Description"} placeholder={"Tax Description"} />
                        </div>
                        <div className="col-sm">
                            <InputWithHead heading={"Tax %"} placeholder={"Tax %"} />
                        </div>
                    </div>
                </div>

            </div>
        )
    }


    ShowVoucherSettingSettingTree = tree => {
        const { TreeNode } = Tree;
        return tree.map((item, catIndex) => {
            return (
                <TreeNode title={this.voucherNode(item)} key={catIndex} >
                    {this.showSubVoucherNode(item, catIndex)}
                </TreeNode>
            );
        });
    };

    voucherNode = item => {
        return (
            <span >
                {item.title}
            </span>
        );
    };

    showSubVoucherNode(item, catIndex) {
        const { TreeNode } = Tree;
        return (
            item.children.map((inItem, scatIndex) => {
                return (
                    <TreeNode
                        title={this.makeSubVoucherNode(inItem)}
                        key={`${catIndex}-${scatIndex}`}
                    >
                    </TreeNode>
                );
            })
        );
    }
    makeSubVoucherNode(item) {
        return (
            <span >
                {item.title}
            </span>
        );
    }




    ///advance  setting view
    advancedSettingView = () => {
        return (
            <div className="inside-form-view">
                <span className='form-heading'>Advanced Setting</span>

                <InputWithHead heading={"Registration Updates"} />
                <Select
                    style={{ width: "100%", paddingRight: 1 }}
                    onChange={(registrationUpdates) => this.setState({ registrationUpdates })}
                    value={this.state.registrationUpdates}
                >
                    <Option value={"weekly"}>Weekly registration updates</Option>
                </Select>
                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Shop integration</Checkbox>
                </div>
                <div style={{ marginTop: 15 }}>
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Invite a friend</Checkbox>
                </div>
                <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Play with a friend</Checkbox>

            </div>
        )
    }

    //disclaimer view
    disclaimerView = () => {
        return (
            <div className="inside-form-view">
                <span className='form-heading'>Disclaimers</span>
                <div className="inside-container-view">
                    <Checkbox className="single-checkbox" defaultChecked={true} onChange={(e) => this.onChange(e)}>Disclaimers</Checkbox>
                    <InputWithHead heading={"Disclaimer Link"} placeholder={"Disclaimer Link"} />
                    <span className='input-heading-add-another'>+ Add Another Disclaimer Link</span>
                </div>
            </div>

        )
    }




    ///setting the available from date
    dateOnChangeFrom = date => {
        // this.setState({ endDate: moment(date).utc().toISOString() })
        console.log(date)
    }

    ////setting the available to date
    dateOnChangeTo = date => {
        console.log(date)
    }


    //////footer view containing all the buttons like submit and cancel
    footerView = () => {
        return (
            <div className="fluid-width" >
                <div className="footer-view">
                    <div className="row" >
                        <div className="col-sm" style={{ display: 'flex', alignItems: "flex-start" }}>
                            <Button type="cancel-button">Cancel</Button>
                        </div>
                        <div className="col-sm" >
                            <div style={{ display: 'flex', justifyContent: "flex-end" }}>
                                <Button className="save-draft-text" type="save-draft-text">Save as Draft</Button>
                                <Button className="save-draft-text" type="save-draft-text">Preview</Button>
                                <Button className="open-reg-button" type="primary">Open Registrations</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }





    render() {
        return (
            <div className="fluid-width" style={{ backgroundColor: "#f7fafc" }} >
                <InnerHorizontalMenu menu={"competition"} />
                <Layout>
                    {this.headerView()}
                    <Content>
                        <div className="formView">
                            {this.contentView()}
                        </div>
                        <div className="formView">
                            {this.UserRegisterView()}
                        </div>
                        <div className="formView">
                            {this.PaymentSettingView()}
                        </div>
                        <div className="formView">
                            {this.advancedSettingView()}
                        </div>
                        <div className="formView">
                            {this.disclaimerView()}
                        </div>

                    </Content>


                    <Footer>
                        {this.footerView()}
                    </Footer>
                </Layout>
            </div>

        );
    }
}
export default CompetitionOpenRegForm;
